@import "variables";
@import "mixins";
.header{
  background: #49b26f;
  .top-header{
    background: rgba(255,255,255,0.15);
    .logo{
      padding: 20px 0;
      display: inline-block;
      float: left;
    }
    ul{
      padding: 0;
      margin: 0;
      li{
        list-style-type: none;
        float: left;
        &:first-child{
          a{
            padding: 20px 0;
            .search-icon{
              padding: 0 20px;
              border-left: 1px solid rgba(255,255,255,0.9);
              border-right: 1px solid rgba(255,255,255,0.9);
            }
          }
        }
        a{
          padding: 20px;
          display: block;
          text-align: center;
          color: #fff;
          &:hover{
            i{
              .opacity(1);
            }
          }
          i{
            font-size: 18px;
            margin-right: 5px;
            .opacity(0.9)
          }
        }
      }
    }
  }
}
.swiper-container {
  width: 100%;
  height: 100%;
  h1{
    font-size: 70px;
    padding: 30px 0 10px 0;
    text-align: center;
    color: #fff;
    font-family: "DIN Alternate";
    letter-spacing: 6px;
    @media (max-width: @screen-md) {
      font-size: 46px;
    }
    span{
      font-size: 50px;
      position: relative;
      top: -8px;
      @media (max-width: @screen-md) {
        font-size: 32px;
      }
    }
  }
  h4{
    text-align: center;
    color: #fff;
  }
  .swiper-wrapper{
    .swiper-slide{
      background-size: cover;
      background-position: center;
      .index-data{
        color: #fff;
        text-align: center;
        border: 1px solid rgba(255,255,255,0.3);
        padding: 15px;
        margin: 30px 50px 80px 50px;
        p{
          span{
            color: #7be08a;
          }
        }
      }
    }
  }
}
.server-info{
  padding: 20px 0;
  background: #fff;
  h2{
    margin-bottom: 20px;
  }
  .row{
    >div{
      margin-bottom: 20px;
      a{
        display: block;
        text-decoration: none;
        text-align: center;
        margin: 10px 60px;
        padding: 15px;
        overflow: hidden;
        &:hover{
          text-decoration: none;
          background: rgba(0,0,0,0.05);
          img{
            transform: scale(1.05);
          }
        }
        img{
          transition: transform .3s ease;
        }
        h4{
          color: #333;
          margin-top: 20px;
        }
        p{
          color: #aaa;
          margin: 0;
          line-height: 24px;
        }
      }
    }
  }
}
.news{
  margin-top: 30px;
  .container{
    .row{
      > div[class*="col-"]{
        > .inner-news{
          background: #fff;
          padding: 20px;
          overflow: hidden;
          @media (max-width: @screen-md) {
            padding: 10px 15px;
          }
          .news-title{
            margin: 10px 0 30px 0;
            @media (max-width: @screen-md) {
              text-align: center;
            }
          }
          ul{
            padding: 0;
            > a.btn-default{
              color: #999;
            }
            li{
              list-style: none;
              border-bottom: 1px solid #eee;
              padding-bottom: 5px;
              margin-bottom: 25px;
              &:nth-child(5){
                border-bottom: none;
                margin-bottom: 0;
              }
              img{
                margin-bottom: 20px;
                @media (max-width: @screen-md) {
                  width: 100%;
                }
              }
              h4{
                margin-top: 0;
              }
              p{
                color: #999;
                &:last-child{
                  color: #ccc;
                }
              }
            }
          }
        }
        .download-app{
          img{
            width: 100%;
          }
          ul{
            padding: 0;
            background: #fff;
            overflow: hidden;
            li{
              list-style-type: none;
              width: 50%;
              display: inline-block;
              float: left;
              margin: 10px 0;
              a{
                color: #333;
                display: block;
                text-align: center;
                span{
                  font-size: 16px;
                }
                &:hover{
                  color: @brand-primary;
                }
              }
              &:first-child{
                border-right: 1px solid #eee;
              }
            }
          }
        }
        .notice{
          padding: 10px 15px 0 15px;
          background: #fff;
          margin-top: 20px;
          h4{
            span{
              a{
                font-size: 14px;
                font-weight: normal;
                color: #aaa;
                &:hover{
                  color: @brand-primary;
                }
              }
            }
          }
          ul{
            padding: 0 0 10px 30px;
            margin-top: 20px;
            border-left: 1px solid #eee;
            li{
              list-style: none;
              position: relative;
              &:after{
                content: "";
                width: 14px;
                height: 1px;
                background: #eee;
                position: absolute;
                left: -30px;
                top: 10px;
              }
              &:before{
                content: "";
                width: 6px;
                height: 6px;
                background: @brand-danger;
                border-radius: 50%;
                position: absolute;
                top: 7px;
                left: -15px;
              }
              a{
                color: #999;
                p:last-child{
                  color: #ccc;
                }
              }
            }
          }
        }
        .contact-us{
          padding: 10px 15px 15px 15px;
          background: #fff;
          margin-top: 20px;
          h4{
            margin-bottom: 22px;
          }
          p{
            color: #999;
            margin-bottom: 7px;
            span{
              color: #ccc;
            }
          }
        }
      }
    }
  }
}
.footer{
  background: #51585e;
  text-align: center;
  margin-top: 60px;
  padding: 40px 0;
  @media (max-width: @screen-md) {
    padding: 20px;
    margin-top: 30px;
  }
  p{
    margin: 0;
    line-height: 30px;
    a,span{
      margin: 0 10px;
      display: inline-block;
    }
    span{
      color: #fff;
    }
  }
}
