.header {
  background: #49b26f;
}
.header .top-header {
  background: rgba(255, 255, 255, 0.15);
}
.header .top-header .logo {
  padding: 20px 0;
  display: inline-block;
  float: left;
}
.header .top-header ul {
  padding: 0;
  margin: 0;
}
.header .top-header ul li {
  list-style-type: none;
  float: left;
}
.header .top-header ul li:first-child a {
  padding: 20px 0;
}
.header .top-header ul li:first-child a .search-icon {
  padding: 0 20px;
  border-left: 1px solid rgba(255, 255, 255, 0.9);
  border-right: 1px solid rgba(255, 255, 255, 0.9);
}
.header .top-header ul li a {
  padding: 20px;
  display: block;
  text-align: center;
  color: #fff;
}
.header .top-header ul li a:hover i {
  opacity: 1;
  filter: alpha(opacity=100);
}
.header .top-header ul li a i {
  font-size: 18px;
  margin-right: 5px;
  opacity: 0.9;
  filter: alpha(opacity=90);
}
.swiper-container {
  width: 100%;
  height: 100%;
}
.swiper-container h1 {
  font-size: 70px;
  padding: 30px 0 10px 0;
  text-align: center;
  color: #fff;
  font-family: "DIN Alternate";
  letter-spacing: 6px;
}
@media (max-width: 992px) {
  .swiper-container h1 {
    font-size: 46px;
  }
}
.swiper-container h1 span {
  font-size: 50px;
  position: relative;
  top: -8px;
}
@media (max-width: 992px) {
  .swiper-container h1 span {
    font-size: 32px;
  }
}
.swiper-container h4 {
  text-align: center;
  color: #fff;
}
.swiper-container .swiper-wrapper .swiper-slide {
  background-size: cover;
  background-position: center;
}
.swiper-container .swiper-wrapper .swiper-slide .index-data {
  color: #fff;
  text-align: center;
  border: 1px solid rgba(255, 255, 255, 0.3);
  padding: 15px;
  margin: 30px 50px 80px 50px;
}
.swiper-container .swiper-wrapper .swiper-slide .index-data p span {
  color: #7be08a;
}
.server-info {
  padding: 20px 0;
  background: #fff;
}
.server-info h2 {
  margin-bottom: 20px;
}
.server-info .row > div {
  margin-bottom: 20px;
}
.server-info .row > div a {
  display: block;
  text-decoration: none;
  text-align: center;
  margin: 10px 60px;
  padding: 15px;
  overflow: hidden;
}
.server-info .row > div a:hover {
  text-decoration: none;
  background: rgba(0, 0, 0, 0.05);
}
.server-info .row > div a:hover img {
  transform: scale(1.05);
}
.server-info .row > div a img {
  transition: transform .3s ease;
}
.server-info .row > div a h4 {
  color: #333;
  margin-top: 20px;
}
.server-info .row > div a p {
  color: #aaa;
  margin: 0;
  line-height: 24px;
}
.news {
  margin-top: 30px;
}
.news .container .row > div[class*="col-"] > .inner-news {
  background: #fff;
  padding: 20px;
  overflow: hidden;
}
@media (max-width: 992px) {
  .news .container .row > div[class*="col-"] > .inner-news {
    padding: 10px 15px;
  }
}
.news .container .row > div[class*="col-"] > .inner-news .news-title {
  margin: 10px 0 30px 0;
}
@media (max-width: 992px) {
  .news .container .row > div[class*="col-"] > .inner-news .news-title {
    text-align: center;
  }
}
.news .container .row > div[class*="col-"] > .inner-news ul {
  padding: 0;
}
.news .container .row > div[class*="col-"] > .inner-news ul > a.btn-default {
  color: #999;
}
.news .container .row > div[class*="col-"] > .inner-news ul li {
  list-style: none;
  border-bottom: 1px solid #eee;
  padding-bottom: 5px;
  margin-bottom: 25px;
}
.news .container .row > div[class*="col-"] > .inner-news ul li:nth-child(5) {
  border-bottom: none;
  margin-bottom: 0;
}
.news .container .row > div[class*="col-"] > .inner-news ul li img {
  margin-bottom: 20px;
}
@media (max-width: 992px) {
  .news .container .row > div[class*="col-"] > .inner-news ul li img {
    width: 100%;
  }
}
.news .container .row > div[class*="col-"] > .inner-news ul li h4 {
  margin-top: 0;
}
.news .container .row > div[class*="col-"] > .inner-news ul li p {
  color: #999;
}
.news .container .row > div[class*="col-"] > .inner-news ul li p:last-child {
  color: #ccc;
}
.news .container .row > div[class*="col-"] .download-app img {
  width: 100%;
}
.news .container .row > div[class*="col-"] .download-app ul {
  padding: 0;
  background: #fff;
  overflow: hidden;
}
.news .container .row > div[class*="col-"] .download-app ul li {
  list-style-type: none;
  width: 50%;
  display: inline-block;
  float: left;
  margin: 10px 0;
}
.news .container .row > div[class*="col-"] .download-app ul li a {
  color: #333;
  display: block;
  text-align: center;
}
.news .container .row > div[class*="col-"] .download-app ul li a span {
  font-size: 16px;
}
.news .container .row > div[class*="col-"] .download-app ul li a:hover {
  color: #49b26f;
}
.news .container .row > div[class*="col-"] .download-app ul li:first-child {
  border-right: 1px solid #eee;
}
.news .container .row > div[class*="col-"] .notice {
  padding: 10px 15px 0 15px;
  background: #fff;
  margin-top: 20px;
}
.news .container .row > div[class*="col-"] .notice h4 span a {
  font-size: 14px;
  font-weight: normal;
  color: #aaa;
}
.news .container .row > div[class*="col-"] .notice h4 span a:hover {
  color: #49b26f;
}
.news .container .row > div[class*="col-"] .notice ul {
  padding: 0 0 10px 30px;
  margin-top: 20px;
  border-left: 1px solid #eee;
}
.news .container .row > div[class*="col-"] .notice ul li {
  list-style: none;
  position: relative;
}
.news .container .row > div[class*="col-"] .notice ul li:after {
  content: "";
  width: 14px;
  height: 1px;
  background: #eee;
  position: absolute;
  left: -30px;
  top: 10px;
}
.news .container .row > div[class*="col-"] .notice ul li:before {
  content: "";
  width: 6px;
  height: 6px;
  background: #d9534f;
  border-radius: 50%;
  position: absolute;
  top: 7px;
  left: -15px;
}
.news .container .row > div[class*="col-"] .notice ul li a {
  color: #999;
}
.news .container .row > div[class*="col-"] .notice ul li a p:last-child {
  color: #ccc;
}
.news .container .row > div[class*="col-"] .contact-us {
  padding: 10px 15px 15px 15px;
  background: #fff;
  margin-top: 20px;
}
.news .container .row > div[class*="col-"] .contact-us h4 {
  margin-bottom: 22px;
}
.news .container .row > div[class*="col-"] .contact-us p {
  color: #999;
  margin-bottom: 7px;
}
.news .container .row > div[class*="col-"] .contact-us p span {
  color: #ccc;
}
.footer {
  background: #51585e;
  text-align: center;
  margin-top: 60px;
  padding: 40px 0;
}
@media (max-width: 992px) {
  .footer {
    padding: 20px;
    margin-top: 30px;
  }
}
.footer p {
  margin: 0;
  line-height: 30px;
}
.footer p a,
.footer p span {
  margin: 0 10px;
  display: inline-block;
}
.footer p span {
  color: #fff;
}
